.user-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    button {
        margin: .5em;
    }
}

.user-inputs {
    margin: .5em;
    width: 40vw;
    display: flex;
    justify-content: space-between;
    input {
        width: 70%;
        color: black;
        padding: 0 .5em;
        font-size: 1em;
    }
    label {
        font-size: 1em;
    }
}