.articles-page {
    height: 100vh;
    width: 100%;
    @include flexColumn;
    h1 {
        margin: 1em 0;
    }
    overflow: hidden;
    .articles-container {
        @include flexColumn;
        overflow: auto;
    }
    .article-container {
        background-color: #fff;
        display: flex;
        flex-direction: column;
        h4,p,h5 {
            color: black;
        }
        padding: 1em;
        margin: 1em 0;
        width: 80%;
        border-radius: 1em;
        transition: .8s;
        &.appears {
            transform: translateX(0);
        }
        h4 {
            align-self: center;
            font-size: 1.3em;
        }
    }
    .paragraph {
        margin: 1em 0;
        height: 6em;
        overflow: hidden;
        &.visible {
            height: auto;
        }
        p {
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
    .comment-container {
        width: 100%;
        display: flex;
        align-items: center;
        margin: .8em 0;
        border: solid .2em black;
        padding: .5em;
        h5 {
            margin-right: .5em;
            font-size: 1.2em;
        }
    }
    .blank {
        width: 100%;
        background-color: #fff;
        height: 2em;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        p {
            font-weight: bold;
            margin-right: 1em;
            cursor: pointer;
        }
    }
    input,label {
        color: black;
    }
    form {
        flex: 1;
        display: flex;
        align-items: center;
    }
    input {
        margin: 0 .5em;
        flex: 1;
        padding: .3em;
    }
    button {
        margin-right: 1em;
    }
}